  text-transform: uppercase;
  font-display: swap;
  font-weight: bold;
  text-shadow: 0 0 10px #ed1864;
}

.jt__row {
  display: block;
}
.jt__row:nth-child(1) {
  -webkit-clip-path: polygon(-10% 75%, 110% 75%, 110% 110%, -10% 110%);
          clip-path: polygon(-10% 75%, 110% 75%, 110% 110%, -10% 110%);
}
.jt__row:nth-child(2) {
  -webkit-clip-path: polygon(-10% 50%, 110% 50%, 110% 75.3%, -10% 75.3%);
          clip-path: polygon(-10% 50%, 110% 50%, 110% 75.3%, -10% 75.3%);
}
.jt__row:nth-child(3) {
  -webkit-clip-path: polygon(-10% 25%, 110% 25%, 110% 50.3%, -10% 50.3%);
          clip-path: polygon(-10% 25%, 110% 25%, 110% 50.3%, -10% 50.3%);
}
.jt__row:nth-child(4) {
  -webkit-clip-path: polygon(-10% 0%, 110% 0%, 110% 25.3%, -10% 25.3%);
          clip-path: polygon(-10% 0%, 110% 0%, 110% 25.3%, -10% 25.3%);
}

.jt__row.jt__row--sibling {
  position: absolute;
  top: 0;
  left: 0;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  
}

.jt__text {
  display: block;
  -webkit-transform-origin: bottom left;
          transform-origin: bottom left;
  -webkit-animation: moveIn 3s 0s cubic-bezier(.36,0,.06,1) alternate infinite ;
          animation: moveIn 3s 0s cubic-bezier(.36,0,.06,1) alternate infinite ;
}
.jt__row:nth-child(1) .jt__text {
  -webkit-transform: translateY(-0.1em);
          transform: translateY(-0.1em);
}
.jt__row:nth-child(2) .jt__text {
  -webkit-transform: translateY(-0.3em) scaleY(1.1);
          transform: translateY(-0.3em) scaleY(1.1);
}
.jt__row:nth-child(3) .jt__text {
  -webkit-transform: translateY(-0.5em) scaleY(1.2) ;
          transform: translateY(-0.5em) scaleY(1.2) ;
}
.jt__row:nth-child(4) .jt__text {
  -webkit-transform: translateY(-0.7em) scaleY(1.3) ;
          transform: translateY(-0.7em) scaleY(1.3) ;
}
.jt__row:nth-child(5) .jt__text {
  -webkit-transform: translateY(-0.9em) scaleY(1.4) ;
          transform: translateY(-0.9em) scaleY(1.4) ;
}
.jt__row:nth-child(6) .jt__text {
  -webkit-transform: translateY(-1.1em) scaleY(1.5) ;
          transform: translateY(-1.1em) scaleY(1.5) ;
}

@-webkit-keyframes moveIn {
  50%, 100% { 
    -webkit-transform: translateY(0em); 
            transform: translateY(0em)
  }
  0%   { 
  opacity: 0; 
  -webkit-filter: blur(10px); 
          filter: blur(10px);
  
  }
  100% { 
  opacity: 1; 
  -webkit-filter: blur(0px); 
          filter: blur(0px);
  }
}

@keyframes moveIn {
  50%, 100% { 
    -webkit-transform: translateY(0em); 
            transform: translateY(0em)
  }
  0%   { 
  opacity: 0; 
  -webkit-filter: blur(10px); 
          filter: blur(10px);
  
  }
  100% { 
  opacity: 1; 
  -webkit-filter: blur(0px); 
          filter: blur(0px);
  }
}



