.staffcontainer {
    width: 100%;
    background-color: black;
    color: white;
  }
  
  .staffcontent {
    width: 95%;
    margin: auto auto;
  }
  .staffitems {
    background-color: #3e3e3e;
    width: 100%;
    padding-right: 3rem;
  }
  .staffbody111{
    padding: 2rem 0;
  }
  
 
  
  .staffitems a {
    text-decoration: none;
    color: white;
  }
  
 
  .staffrole {
      display: grid;
      grid-template-columns: repeat(1, 3fr);
      padding-left: 0.4rem;
  }
  
  .staffheader p {
    padding-bottom: 1.5rem;
  }
  
  .staffheader h1 {
    text-transform: uppercase;
    font-weight: bolder;
    font-size: 2rem;
  }
  
  .staffheader p {
    color: #ed1846;
  }
  .staffimage {
    height: 6.5rem;
  }
  .staffimage img {
    height: 6.5rem;
  }
  
  .arrow img {
    height: 2rem;
    display: none;
  }
  .staffdetails {
    display: flex;
  }
  
  .sypmphoniiname {
    display: flex;
    align-items: flex-end;
  }
  .sypmphoniiposition {
    font-weight: lighter;
    color: #ed1846;
  }
  
 
  
  .staffitems:hover .arrow img {
    display: block;
  }
  
  .staffitems:hover .staffheader p {
    color: #3e3e3e;
  }
  
  .staffitems:hover .sypmphoniiposition {
    color: black;
  }
  
  
  @media screen and (max-width: 992px) {
      .stavecarry {
          display: grid;
          grid-template-columns: repeat(2, 1fr);
          gap: 10px;
        }
  
  }
  
  @media screen and (max-width: 605px) {
      .stavecarry {
          display: grid;
          grid-template-columns: repeat(1, 1fr);
          gap: 10px;
          
        }
  
        .staffitems{
            width: 100%;
        }
  
  }